@import "../../globals.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    margin-top: 50px;
    font-size: 50px;
  }

  ul {
    margin: 30px;
    padding: 0px;
    list-style: none;
    display: flex;
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    .item {
      width: 300px;
      height: 200px;
      border-radius: 20px;
      border: 1px solid lightgray;
      margin: 20px 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

      h3 {
        position: absolute;
        font-size: 20px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.3;
          z-index: 0;
        }
      }
    }
  }
}
